.seccion{
    width: 100%;
    margin: 0 0 1em;
    min-height: 150vh;
  /*  background: lightblue;*/
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    text-align: center;
    z-index: 2;
    position: relative;

}

.seccion .h2{
    margin: 0 0 1rem;
    font-size: 4em;
    font-style: italic;
}
.seccion .p{

}



.seccion .grid{
   width: 90%;
 /*  background: lightcoral;*/
   display: grid;
   grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
   gap: 3em;
   margin: 1em 0;
   max-width: 40em;
   

}

.seccion .article{
    width: 100%;
    min-height: 40vh;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
    border-radius: 15px;
}

.seccion .a{

}

.seccion .img{            /*figure*/
   width: 100%; 
   background:beige ;

}

.seccion h4{
   
    width: 100%;
    height:100%;
    background-color: rgba(0, 0, 0, .5);
    backdrop-filter: blur(1rem);
    position: absolute;
    bottom: -21px;
    left: 0;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;

    opacity: 0;
    transition: all .5 ease;

    color: white ;
    font-size: 1em;
    font-style: italic;
    transform: translatez(100%);
    transition: all .5s ease;
    
}

.seccion .article:hover h4{
    opacity: 1;
    transform: translatez(0%);
}
