.section1{
    min-height: 100vh;
    position: relative;
    align-items: center;
    justify-content: center;
    z-index: 2;
  
}

.circulos{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    filter: blur(20px);
    animation: circulo1 70s linear infinite both;
}
    

.circulo1{
    position: absolute;
    width: 60%;
    margin-top: -100px;
    margin-left: -400px;
}


.circulo1 .path{
    fill:#2C1F59;

    
      
  }

.circulo2{
    position: absolute;
    width: 30%;
    margin-top: 50px;
    margin-right: -400px;   
}

.circulo2 .path{
    fill: #B4FEE7; 
}

.circulo3{
    position: absolute;
    width: 50%;
    margin-top: 300px;
    margin-left: -100px;
}

.circulo3 .path{
    fill: #5EF5E5;
}

.circulo4{
    position: absolute;
    width: 50%;
    margin-top: 220px;
    margin-left:-400px;
}

.circulo4 .path{
    fill:#FD49A0;
}

   

.section_name .h2{  
   position: absolute;
   color: black;
   margin: 20vh;
   margin-left: 450px;
   font-size: 5em;
   font-style: oblique;
   font-weight: 600;
   z-index: 2;
}
  


.section_name .h3{
    position: absolute;
    color: black;
    margin: 40vh;
    margin-left: 500px;
    font-size: 4em;
    font-style: italic;
    font-weight: 450;
    z-index: 2;
    
    
}

.section_name .ul{
    
    position: absolute;
    list-style: none;
    margin-top: 450px;
    margin-left: 520px;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    font-weight: 600;
    z-index: 2;
}


.section_name .ul .li{ 
    margin: 0 1em;
    
}

.section_name  .ul .a{
    color: black;
    text-decoration: none;
    text-transform: uppercase;
    border: 2px solid rgba(0, 0, 0, .5);
    padding: 1em;
    border-radius: 6%;
    transition: all .5s ease;
  }


  .section_name .ul .a:hover{
    border-color:white;
  }

  .section_name .li .a:hover{
    color: white;
  }



  @keyframes circulo1{
     
    from{
       transform: rotate(0deg) scale(3);  
     }
     to{
         transform: rotate(360deg) scale(3);
     }
 }