.header{
    width: 93.7%;
    min-width: 5em;
    background-color: lightblue;
    padding: 1em 3em;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
    display: none;
}

.header h1{
    

}

.header h1 a {
 text-decoration: none;

}

.header .nav{
   
    margin-right: 5em; 
}

.header .ul{
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: space-between;
    list-style: none;
    
}

.header .li{
    margin: 0 1em;
   
}

.header .li .a{
    text-decoration: none;
    
}